<template>
  <svg class="bd-placeholder-img" :class="clas" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice"
    focusable="false" role="img" :width="width" :height="height">
    <title v-if="title">{{title}}</title>
    <rect width='100%' height='100%' :fill="background" />
    <!-- x,y只在SVG元素里面使用，其他元素不能用 -->
    <text v-if="textvalue" x="50%" y="50%" :fill="color" dy="0.3em">{{textvalue}}</text>
  </svg>
</template>

<script>
  export default {
    props: {
      title:{
        type:String,
        default:'Placeholder'
      },
      text: String, //默认值：width x height
      clas: String, //默认值：'bd-placeholder-img'
      color:{
        type:String,
        default:'#dee2e6'   //默认值
      },
      background:{
        type:String,
        default:'#868e96'   //默认值
      },
      width:{
        type:String,
        default:'100%'      //默认值
      },
      height:{
        type:String,
        default:'180%'
      },
    },
    computed: {
      textvalue: function() {
        if (this.text ===undefined)
          return `${this.width} x ${this.height}`
        else
          return this.text
      }
    },


  }
</script>

<style lang="scss">
  .bd-placeholder-img{
    font-size: 1.125rem;
    text-anchor: middle; //SVG中使字体居中
  }
  @media (min-width:768px) {
    .bd-placeholder-img-lg{
      font-size: 3.5rem;
    }
  }
</style>
